@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap);
.weather-search__form {
    text-align: center;
    margin-top: 3rem;
    margin-bottom: 5rem;
}
.weather-search__input {
    display: inline-block;
      border: solid 2px transparent;
      background-color: rgba(229, 57, 53, .1) !important;
      padding: 0.5rem 3rem;
      border-radius: 2px;
      margin-right: 2rem;
      padding-left: 1rem;
      padding-right: 4rem;
}
.weather-search__input:focus {
    outline: none;
    border-bottom: solid 2px #E53935;
}
.weather-search__submit {
    display: inline-block;
     text-align: center;
     background-color: transparent;
     font-size: 3rem;
     height: 30px;
     width: 30px;
     position: relative;
     top: 0.9rem;
     margin-left: -6rem;
}
.weather-search__button {
    padding: 1.2rem;
      color: #E53935;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
}
.weather-search__button:focus {
    outline: none;
}
